body {
  background-color: #fff;
  height: 100%;
  width: 100%;
  overflow: hidden;
  margin: 0px !important;
  padding: 0px !important;
  display: flex;
  flex-direction: column;
}

@font-face {
  font-family: 'Ubuntu';
  src: local('Ubuntu'), url(./fonts/ubuntu.ttf) format('truetype');
}

@font-face {
  font-family: 'Britney';
  src: local('Britney'), url(./fonts/britney.otf) format('opentype');
}

main {
  margin-top: 125px;
  display: flex;
  flex-direction: column;
}

#profile {
  display: block;
  margin-left: auto;
  margin-right: auto;
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.2);
  border-radius: 100px;
  max-width: 100px;
  max-height: 100px;
  z-index: 1;
}

a {
  text-decoration: none;
}

h1 {
  font-family: Ubuntu;
  font-size: 15px;
  text-align: center;
  color: #666666;
  margin-top: 20px;
}

h2 {
  font-family: Britney;
  font-size: 20px;
  font-weight: 100;
  text-align: center;
  color: #666666;
}

h3 {
  font-family: Ubuntu;
  font-size: 15px;
  text-align: center;
  color: #666666;
}

section {
  margin-top: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding-left: 10%;
  padding-right: 10%;
}

.button {
  width: 100%;
  height: 48px;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.5) 0%, rgba(238, 238, 238, 0.5) 100%);
  border: 2px solid #AFB1AE;
  border-radius: 100px;
  margin-top: 10px;
  margin-bottom: 10px;
  max-width: 500px;
  transition: .2s;
  z-index: 1;
}

.button:hover {
  transform: translate(0, -3px);
  transition: .2s;
}

#spacer {
  margin-top: 50px;
}

#img1 {
  position: absolute;
  top: -15%;
  align-self: center;
  justify-self: center;
}

#img2 {
  position: absolute;
  top: 41.71%;
  align-self: center;
  justify-self: center;
}

@media only screen 
  and (max-device-width: 1000px) {
      
      body {
          overflow-y: auto;
          overflow-x: hidden;
          margin: 0px !important;
      }

      main {
          margin-top: 15%;
      }
      
      section {
          margin-top: auto;
      }

      #img1 {
          top: -35%;
      }

      #img2 {
          max-width: 100%;
      }

      #spacer {
          margin-top: 10%;
      }
  }